.gotoTop {
  background: url("../../images/home.png") no-repeat scroll -135px -249px #666;
  border-radius: 4px 4px 4px 4px;
  display: block;
  float: left;
  height: 50px;
  outline: 0 none;
  text-indent: -9999em;
  width: 50px;
  position: absolute;
  text-align: center;
  right: -51px;
  bottom: 10px;
  _position: absolute !important;
  right: 30px;
  bottom: 80px;
}
.gotoTopHover {
