}
#airwheel_weixin{position:fixed;width:100%;height:100%;background:rgba(0,0,0,0.5);top:0;z-index:9999;display:none
}
#airwheel_weixin .weixin{width:15rem;height:18.5rem;background:#fff;border-radius:.5rem;position:fixed;top:50%;left:50%;margin-left:-7.5rem;margin-top:-9rem;overflow:hidden;text-align:center;color:rgba(0,0,0,0.6);transform:scale(1, 0);transition:all 300ms
}
#airwheel_weixin .weixin.prompt_show{animation:prompt_show 300ms ease-in-out;animation-fill-mode:forwards;transform-origin:top
}
@keyframes prompt_show{
0%{transform:scale(1, 0)
}
100%{transform:scale(1, 1)
}
}


